
header {
  position: absolute;
  width: 1440px;
  height: 70px;
  left: 0px;
  top: 0px;
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);

}

.container {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 769px;
  height: 21px;
  left: 531px;
  top: 26px;


}

.header_link {
  text-decoration: none;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;

  display: flex;
  align-items: center;


  color: rgba(31, 32, 65, 0.5);
}

.material-icons {
  width: 24px;
  height: 21px;
  left: 643px;
  top: 26px;

  font-family: Material Icons;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  color: rgba(31, 32, 65, 0.5);
}

.button {
  position: relative;
  width: 87px;
  height: 34px;
  padding: 2px;

  border-radius: 22px;
  border: transparent;
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);

}

.button-container {
  position: absolute;
  left: -2px;


  z-index: 0;
  width: 100%;
  height: 100%;



}

.button-text {
  padding: 9px 20px 6px 20px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  /* identical to box height */

  text-transform: uppercase;

  /* Primary Color */

  color: #BC9CFF;
  background-color: white;
  border-radius: 22px;

}

.button-registration {
  width: 196px;
  height: 34px;
  left: 1104px;
